<script>
// This example is from Connor Rothschild's Newline Course
// You can find it here https://www.newline.co/courses/better-data-visualizations-with-svelte

  import AxisX from "./AxisX.svelte";
  import AxisY from "./AxisY.svelte";
  import Legend from "./Legend.svelte";
  import Tooltip from "./Tooltip.svelte";
  import data from "./data.js";
  import { forceSimulation, forceX, forceY, forceCollide } from "d3-force";
  import { scaleLinear, scaleBand, scaleOrdinal, scaleSqrt } from "d3-scale";
  let width = 400,
    height = 400;
  const margin = { top: 0, right: 0, left: 0, bottom: 20 };
  $: innerWidth = width - margin.left - margin.right;
  let innerHeight = height - margin.top - margin.bottom;
  import { mean, rollups, extent } from "d3-array";
  // Generate the average for each grade, so that we can sort according to that
  const grade = rollups(
    data,
    v => mean(v, d => d.estimate),
    d => d.grade
  ) // Group data by grade and return the group-wide mean
    .sort((a, b) => a[1] - b[1]) // Sort according to value
    .map(d => d[0]); // Grab the grade
  const colorRange = [
    "#dda0dd",
    "#fe7f2d",
    "#fcca46"
  ];
  let colorScale = scaleOrdinal()
    .domain(grade) 
    .range(colorRange);
  $: radiusScale = scaleSqrt()
    .domain([0, 65]) // The same domain passed to xScale
    .range(width < 568 ? [2, 6] : [3, 8]);
  $: xScale = scaleLinear()
    .domain([0, 65]) // Alternatively, we could pass .domain(extent(data, d => d.happiness))
    .range([0, innerWidth]);
  let yScale = scaleBand()
    .domain(grade)
    .range([innerHeight, 0])
    .paddingOuter(0.5);
  let simulation = forceSimulation(data);
  let nodes = [];
  simulation.on("tick", () => {
    nodes = simulation.nodes();
  });
  $: {
    simulation
      .force(
        "x",
        forceX()
          .x(d => xScale(d.estimate))
          .strength(0.8)
      )
      .force(
        "y",
        forceY()
          .y(d => (groupByGrade ? yScale(d.grade) : innerHeight / 2))
          .strength(0.2)
      )
      .force("collide", forceCollide().radius(d => radiusScale(d.estimate)))
      .alpha(0.3) // [0, 1] The rate at which the simulation finishes. You should increase this if you want a faster simulation, or decrease it if you want more "movement" in the simulation.
      .alphaDecay(0.0005) // [0, 1] The rate at which the simulation alpha approaches 0. you should decrease this if your bubbles are not completing their transitions between simulation states.
      .restart();
  }
  // Highlight hovered circles and deemphasize others
  // If a continent is hovered (from Legend.svelte), highlight relevant circles
  let hovered, hoveredGrade;
  import { fade } from "svelte/transition";
  // Boolean for whether the circles are grouped by continent
  let groupByGrade = false;
</script>

<h1>Lifetime Drug Use Among American Youth in 2022</h1>
<Legend {colorScale} bind:hoveredGrade />
<div
  class="chart-container"
  bind:clientWidth={width}
  on:click={() => {
    groupByGrade = !groupByGrade;
    hovered = null;
  }}
>
  <svg {width} {height}>
    <!-- Reference line -->
    {#if hovered}
      <line
        transition:fade
        x1={hovered.x}
        x2={hovered.x}
        y1={height - margin.bottom}
        y2={hovered.y + margin.top + radiusScale(hovered.estimate)}
        stroke={colorScale(hovered.grade)}
        stroke-width="2"
      />
    {/if}
    <g
      class="inner-chart"
      transform="translate({margin.left}, {margin.top})"
      on:mouseleave={() => (hovered = null)}
    >
      <AxisX {xScale} height={innerHeight} width={innerWidth} />
      <AxisY {yScale} {groupByGrade} />
      {#each nodes as node, i}
        <circle       
          in:fade={{ delay: 200 + 10 * i, duration: 400 }}
          cx={node.x}
          cy={node.y}
          r={radiusScale(node.estimate)}
          fill={colorScale(node.grade)}
          title={node.drug}
          opacity={hovered || hoveredGrade
            ? hovered === node || hoveredGrade === node.grade
              ? 1
              : 0.3
            : 1}
          stroke={hovered || hoveredGrade
            ? hovered === node || hoveredGrade === node.grade
              ? "black"
              : "transparent"
            : "#00000090"}
          on:mouseover={() => (hovered = node)}
          on:focus={() => (hovered = node)}
          tabindex="0"
          on:click={(event) => {
            event.stopImmediatePropagation();
          }}
        />
      {/each}
    </g>
  </svg>
  {#if hovered}
    <Tooltip data={hovered} {colorScale} {width} />
  {/if}
</div>

<style>
  :global(*) {
    font-family: Inter, -apple-system, system-ui;
    -moz-osx-font-smoothing: grayscale;
  }
  :global(.tick text, .axis-title) {
    font-size: 12px; /* How big our text is */
    font-weight: 400; /* How bold our text is */
    fill: hsla(212, 10%, 53%, 1); /* The color of our text */
    user-select: none; /* Prevents text from being selected */
  }
  h1 {
    margin: 0 0 0.5rem 0;
    font-size: 1.35rem;
    font-weight: 600;
    text-align: center;
  }
  circle {
    transition: stroke 300ms ease, opacity 300ms ease;
    cursor: pointer;
  }
</style>