<script>
  export let yScale;
  export let groupByGrade;
  $: ticks = yScale.domain();
  import { fade } from "svelte/transition";
</script>

<g class="axis y">
    {#each ticks as tick, index}
      {#if groupByGrade}
          <g class='tick' in:fade={{delay: index * 50}} out:fade={{duration: 100}}>
            <text y={yScale(tick) + 30}>{tick}</text>
          </g>
      {/if}
    {/each}
</g>